import React, { Component } from "react";

import firebase from "../../firebase";

import "./main.scss";

class ContactMe extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      subject: "",
      email: "",
      message: "",
    };
  }

  handleChange = (event) => {
    const { name, value, type, checked } = event.target;
    type === "checkbox"
      ? this.setState({
          [name]: checked,
        })
      : this.setState({
          [name]: value,
        });
  };

  handleSubmit = (event) => {
    event.preventDefault();
    firebase
      .firestore()
      .collection("github-messages")
      .add({
        name: this.state.name,
        subject: this.state.subject,
        email: this.state.email,
        message: this.state.message,
      })
      .then(() => {
        this.setState({
          name: "",
          subject: "",
          email: "",
          message: "",
        });
        alert(`Message Sent !`);
      });
  };

  render() {
    return (
      <div>
        <section className="about-section section">
          <div className="container">
            <div className="row">
              <div className="col-sm-4">
                <div className="heading">
                  <h3>
                    <b>Contact Me</b>
                  </h3>
                  <h6 className="font-lite-black">
                    <b>Have a Project?</b>
                  </h6>
                  <br />
                </div>
              </div>
              <div className="col-sm-8">
                <div>
                  <form
                    action="#"
                    className=" p-4 p-md-5 contact-form"
                    onSubmit={this.handleSubmit}
                  >
                    <div className="row">
                      <div className="col-md-6">
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control"
                            placeholder="Your Name"
                            name="name"
                            value={this.state.name}
                            onChange={this.handleChange}
                            required={true}
                          />
                        </div>
                      </div>
                      <div className="col-md-6">
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control"
                            placeholder="Your Email"
                            name="email"
                            value={this.state.email}
                            onChange={this.handleChange}
                            required={true}
                          />
                        </div>
                      </div>
                      <div className="col-md-12">
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control"
                            placeholder="Subject"
                            name="subject"
                            value={this.state.subject}
                            onChange={this.handleChange}
                            required={true}
                          />
                        </div>
                      </div>
                      <div className="col-md-12">
                        <div className="form-group">
                          <textarea
                            id=""
                            cols="30"
                            rows="7"
                            className="form-control"
                            placeholder="Message"
                            name="message"
                            value={this.state.message}
                            onChange={this.handleChange}
                            required={true}
                          ></textarea>
                        </div>
                      </div>
                      <div className="col-md-12">
                        <div className="form-group" align="right">
                          <input
                            id="send_btn"
                            type="submit"
                            value="Send Message"
                            className="btn btn-primary py-3 px-6"
                          />
                        </div>
                      </div>
                    </div>
                  </form>
                  <div className="row addresses">
                    <div className="dbox w-100 d-flex ">
                      <div className="icon d-flex align-items-center justify-content-center">
                        <span className="icon ion-ios-location"></span>
                      </div>
                      <div className="text">
                        <p>
                          <span>Address:</span> Berhampore, West Bengal, IN
                          742134
                        </p>
                      </div>
                    </div>
                    <div className="dbox w-100 d-flex">
                      <div className="icon d-flex align-items-center justify-content-center">
                        <span className="icon ion-ios-telephone"></span>
                      </div>
                      <div className="text">
                        <p>
                          <span>Phone:</span>{" "}
                          <a href="tel://6296391595">+91 6296 3915 95 </a>
                        </p>
                      </div>
                    </div>
                    <div className="dbox w-100 d-flex">
                      <div className="icon d-flex align-items-center justify-content-center">
                        <span className="icon ion-ios-chatboxes"></span>
                      </div>
                      <div className="text">
                        <p>
                          <span>Email:</span>{" "}
                          <a href="mailto:mrinjamul@gmail.com">
                            mrinjamul@gmail.com
                          </a>
                        </p>
                      </div>
                    </div>
                    <div className="dbox w-100 d-flex">
                      <div className="icon d-flex align-items-center justify-content-center">
                        <span className="icon ion-planet"></span>
                      </div>
                      <div className="text">
                        <p>
                          <span>Website</span>{" "}
                          <a href="https://www.injamul.tk/">injamul.tk</a>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default ContactMe;
